@import url(https://fonts.googleapis.com/css2?family=Krona+One&display=swap);


h1, a, span, li , div {
  font-family: Arial, Geneva, Helvetica, sans-serif;
  text-decoration: none;
}

.App {
  text-align: center;
}

.page {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.content {
  position: relative;
  top: 4rem;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
}

section {
  width: 100%;
  height: 100%;
}

.page-format {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100%;
}

.page-content {
  width: 1250px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header-container {
  z-index: 99;
  position: fixed;
  width: 100%;
  background-color: black;
  color: white;
}

.header-content {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem;
}

.menu-icon {
  color: white;
  font-size: 2rem;
}

.menu-icon:hover {
  -webkit-animation: burger 5s linear infinite;
          animation: burger 5s linear infinite;
}

@-webkit-keyframes burger {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

@keyframes burger {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
  }
}

.dropdown {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: rgb(129, 129, 255, 0.9);
  padding: 1rem;
  height: 100%;
  width: 100%;
}

#header-menu {
  display: none;
}

.dropdown-container {
  position: fixed;
  top: -17rem;
  z-index: 20;
  width: 100%;
  transition: all .7s ease-in-out;
}

.header-link {
  padding: .5rem 0rem;
  color: white;
  margin: .2rem;
  width: 100%;
  padding: 0 1rem;
  font-weight: bold;
}

#header-menu:checked ~ .dropdown-container {
  top: 4rem;
  transition: all .7s ease-in-out;
}
.about-content {
  color: black;
}

.about-profile-img {
  height: 20rem;
  width: 20rem;
  object-fit: cover;
}

.portfolio-content {
  color: white
}
.contact-content {
  color: white;
}
.resume-content {
  color: black;
}
.home-font {
  font-size: 5rem;
  font-weight: bold;
  color: white;
}

.home-content {
  color: white;
}
.checkbox {
  display: inline;
  display: initial;
  position: relative;
  z-index: 200;
  left: 10rem;
}

.home-page, .portfolio-page, .contact-page {
  background-color: black;
  -webkit-animation: return 1s linear;
          animation: return 1s linear;
  height: 100%;
}

.about-page, .resume-page {
  background-color: white;
  -webkit-animation: return 1s linear;
          animation: return 1s linear;
  height: 100%;
}

@-webkit-keyframes return {
  from {
    -webkit-transform: translateX(100%);
            transform: translateX(100%)
  }
  to {
    -webkit-transform: translateX(0%);
            transform: translateX(0%)
  }
}

@keyframes return {
  from {
    -webkit-transform: translateX(100%);
            transform: translateX(100%)
  }
  to {
    -webkit-transform: translateX(0%);
            transform: translateX(0%)
  }
}

